@import url(https://fonts.googleapis.com/css2?family=Gothic+A1&family=IBM+Plex+Sans&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  margin: 0 auto;
  font-family: "IBM Plex Sans", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.app {
  min-width: 100vw;
  min-height: 100vh;
  background: #f2f3f4;
}

.scrollNews {
  margin: 4vh 0 2vh 0;
}

.scrollNews {
  display: flex;
  overflow-x: scroll;
  -ms-scroll-snap-type: x mandatory;
      scroll-snap-type: x mandatory;
}

.articleContainer {
  min-width: 60vw;
  height: 32vh;
  border-radius: 20px;
  margin-left: 4vw;
  scroll-snap-align: center;
  position: relative;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.articleInfo {
  width: 88%;
  border-radius: 20px;
  padding: 6% 8%;
  position: absolute;
  background-color: white;
  margin: 8% auto;
  bottom: 0;
  right: 0;
  left: 0;
}

#title {
  font-size: 3.4vw;
  color: #222;
  font-weight: 600;
}

.subHeading {
  width: 100%;
  display: inline-block;
  margin: 1vh 0 -4px 0;
  font-size: 2.4vw;
}

#ctgType {
  color: #5a9bd6;
  text-transform: uppercase;
  font-weight: 300;
  float: left;
}

#datePublished {
  color: #707070;
  font-weight: 300;
  float: right;
}

.subArticleContainer {
  /* border: 1px solid black; */
  height: 28vw;
  width: 92vw;
  margin: 0 auto;
  position: relative;
  display: flex;
}

.subArticleContainer img {
  position: absolute;
  left: 0;
  top: 10%;
  width: 32%;
  height: 80%;
  border-radius: 20px;
}

.subArticleInfo {
  position: absolute;
  display: flex;
  flex-direction: column;
  /* align-items: center; */
  /* border: 1px solid black; */
  justify-content: center;
  right: 0;
  top: 10%;
  width: 64%;
  height: 80%;
}

.othersubHeading {
  font-size: 2.4vw;
  /* border: 1px solid black; */
}

#title-sub-article {
  font-size: 3.2vw;
  /* border: 1px solid black; */
  color: #222;
  font-weight: 600;
  margin-bottom: 8px;
}

#ctgType-sub-article {
  color: #5a9bd6;
  text-transform: uppercase;
  font-weight: 300;
  float: left;
}

#date-published-sub-article {
  color: #707070;
  font-weight: 300;
  float: right;
}

.headerContainer {
  position: relative;
  width: 100vw;
  height: 16vh;
  margin-top: 2vh;
  /* border: 1px solid black; */
}

.logo {
  position: absolute;
  display: flex;
  height: 80%;
  left: 0;
  right: 0;
  margin: 0 auto;
  /* border: 1px solid black; */
}

.headerContainer img {
  height: 100%;
  max-width: 100%;
  position: absolute;
  align-self: center;
  left: 0;
  right: 0;
  margin: 0 auto;
  height: 100%;
}

.curDay {
  position: absolute;
  display: flex;
  height: 20%;
  right: 0;
  bottom: 0;
  margin-right: 6vw;
  /* border: 1px solid black; */
}

.curDay h4 {
  font-size: 3.2vw;
  font-weight: 300;
  color: #707070;
}

