.subArticleContainer {
  /* border: 1px solid black; */
  height: 28vw;
  width: 92vw;
  margin: 0 auto;
  position: relative;
  display: flex;
}

.subArticleContainer img {
  position: absolute;
  left: 0;
  top: 10%;
  width: 32%;
  height: 80%;
  border-radius: 20px;
}

.subArticleInfo {
  position: absolute;
  display: flex;
  flex-direction: column;
  /* align-items: center; */
  /* border: 1px solid black; */
  justify-content: center;
  right: 0;
  top: 10%;
  width: 64%;
  height: 80%;
}

.othersubHeading {
  font-size: 2.4vw;
  /* border: 1px solid black; */
}

#title-sub-article {
  font-size: 3.2vw;
  /* border: 1px solid black; */
  color: #222;
  font-weight: 600;
  margin-bottom: 8px;
}

#ctgType-sub-article {
  color: #5a9bd6;
  text-transform: uppercase;
  font-weight: 300;
  float: left;
}

#date-published-sub-article {
  color: #707070;
  font-weight: 300;
  float: right;
}
